const ProjectList = () => {
  return (
    <>
      {projects.map((item) => (
        <div key={item.id}>
          <h4>{item.name}</h4>
          <h5>{item.type}</h5>
          <p>{item.description}</p>
          {item.images != null ? (
            <a href={item.images} className="un">
              Zobacz galerię zdjęć &gt;
            </a>
          ) : (
            ""
          )}
          {item.link != null ? (
            <a href={item.link} className="un">
              Przejdź do strony &gt;
            </a>
          ) : (
            ""
          )}
          {item.repo != null ? (
            <a href={item.repo} className="un">
              Sprawdź repozytorium &gt;
            </a>
          ) : (
            ""
          )}
        </div>
      ))}
    </>
  );
};

export default ProjectList;
